<template>
  <div>
  <div class="block">
    <el-row>
      <span class="demonstration">年</span>
    <el-date-picker
      value-format="yyyy"
      v-model="yearValue"
      type="year"
      placeholder="选择年">
    </el-date-picker>


    <span class="demonstration"> 曲线:</span>
    <!-- 图表内容 label不设置则等同于value的值-->
    <el-select @change="changeLine()" v-model="lineValue" placeholder="选择曲线">
      <el-option
      v-for="item in yearOptions"
      :key="item.value"
      :value="item.value">
      </el-option>

    </el-select>


    <!-- 导出功能 -->
    <el-dropdown  @command="handleCommand">
      <el-button type="primary">
        导出<i class="el-icon-arrow-down el-icon--right"></i>
      </el-button>
      <el-dropdown-menu slot="dropdown">
      <el-dropdown-item v-print="'#YearChart'">打印图表</el-dropdown-item>
      <el-dropdown-item command ="a">导出PDF格式</el-dropdown-item>
      <el-dropdown-item>导出JPEG格式</el-dropdown-item>
      <el-dropdown-item>导出PNG格式</el-dropdown-item>
      </el-dropdown-menu>
    </el-dropdown>

    <!-- 选择显示线段 -->
    <el-checkbox-group @change="addLineYear()" v-model="checkListYear">
      <el-checkbox :style="isshowYear" label="A相"></el-checkbox>
      <el-checkbox :style="isshowYear" label="B相"></el-checkbox>
      <el-checkbox :style="isshowYear" label="C相"></el-checkbox>
      <el-checkbox :style="isshowYear0" label="总体"></el-checkbox>
      <el-checkbox :style="isshowYear1" label="需量负荷"></el-checkbox>
      <el-checkbox :style="isshowYear2" label="零线电流"></el-checkbox>
      <el-checkbox :style="isshowYear3" label="Uab"></el-checkbox>
      <el-checkbox :style="isshowYear3" label="Ubc"></el-checkbox>
      <el-checkbox :style="isshowYear3" label="Uca"></el-checkbox>
      <el-checkbox :style="isshowYear4" label="电流"></el-checkbox>
      <el-checkbox :style="isshowYear4" label="电压"></el-checkbox>
    </el-checkbox-group>

    </el-row>
  </div>
  <div class="Chart">
    <h2 style="text-align: center">{{content.data}}-{{lineValue}}曲线({{yearValue}})</h2>
  </div>

  <div id="myChartYear"
  :style="{ width: '1000px', height: '500px'}"
  ></div>
    


  <div>
    <el-row>
      <el-button :class="{activedYear:changeBG[0]}" size="mini" @click="fhclick()" >负荷</el-button>
      <el-button :class="{activedYear:changeBG[1]}" size="mini" @click="wgglclick()" >无功功率</el-button>
      <el-button :class="{activedYear:changeBG[2]}" size="mini" @click="glysclick()" >功率因数</el-button>
      <el-button :class="{activedYear:changeBG[3]}" size="mini" @click="dlclick()" >电流</el-button>
      <el-button :class="{activedYear:changeBG[4]}" size="mini" @click="dyclick()" >电压</el-button>
      <el-button :class="{activedYear:changeBG[5]}" size="mini" @click="dlxbclick()" >电流谐波</el-button>
      <el-button :class="{activedYear:changeBG[6]}" size="mini" @click="dyxbclick()" >电压谐波</el-button>
      <el-button :class="{activedYear:changeBG[7]}" size="mini" @click="dypcclick()" >电压偏差</el-button>
      <el-button :class="{activedYear:changeBG[8]}" size="mini" @click="sxclick()" >三相不平衡</el-button>

    </el-row>
  </div>  

  <div>
    <el-table :data="tableData"  style="width: 1150px">
      <el-table-column prop="message" label="信息" width="150"></el-table-column>
      <el-table-column prop="average" label="平均值" width="200"></el-table-column>
      <el-table-column label="最大值" width="400">
        <el-table-column prop="maxValue" label="值" width="150"></el-table-column>
        <el-table-column prop="maxTime" label="时间" width="250"></el-table-column>
      </el-table-column>
      <el-table-column label="最小值" width="400">
        <el-table-column prop="minValue" label="值" width="150"></el-table-column>
        <el-table-column prop="minTime" label="时间" width="250"></el-table-column>
      </el-table-column>

    </el-table>
  </div>




  </div>
</template>


<script>

const echarts = require("echarts");
import {toDaynodeData} from'@/components/ElectricEnergyAnalysis.vue';
import axios from 'axios'

export default {
  name: 'Chart',
  data(){
    return{
      isshowYear:'',
      isshowYear0:'',
      isshowYear1:'display:none',
      isshowYear2:'display:none',
      isshowYear3:'display:none',
      isshowYear4:'display:none',
      
      content:toDaynodeData,
      //后端数据容器
      yearChartdata:[],
      yearChartdataMax:[],

      tableAvgDataYear:[],
      tableMaxDataYear:[],
      tableMinDataYear:[],

      //时间参数
      yearValue:'',
      //曲线参数
      lineValue:'平均负荷',
      //曲线选择
      yearOptions: [{
        value: '平均负荷',
      }, {
        value: '最大负荷',
      },{
        value: '无功功率',
      }, {
        value: '功率因数',
      }, {
        value: '电流',
      }, {
        value: '电压',
      }, {
        value: '温度',
      }, {
        value: '电流谐波畸变率',
      }, {
        value: '电压谐波畸变率',
      }, {
        value: '频率偏差',   
      }, {
        value: '电压偏差',   
      }, {
        value: '三相不平衡度',   
      }],
      checkListYear:['总体'],

      echartsOption:{
        legend: {
          data: ['总体'],
          selected:{'A相':false, 'B相':false, 'C相':false, '总体':true,'需量负荷':false,'零线电流':false,'Uab':false,'Ubc':false,'Uca':false,
                    '电流':false,'电压':false}
        },
        xAxis:{
          type: 'category',
          data: [],   // x轴数据
          name: '年份',   // x轴名称
          nameTextStyle: {	// x轴名称样式
            fontWeight: 600,
            fontSize: 18
          }
        },
        yAxis:{
          type:'value',
          min:0,
          name: '平均负荷',   // x轴名称
          nameTextStyle: {	// x轴名称样式
            fontWeight: 600,
            fontSize: 18
          }
        },
        tooltip: {
          //鼠标放到图上的数据展示样式
          trigger: "axis",
        },
        series: [{
            //series中加入每个line的数据
            name: 'A相',
            data: [],
            type: 'bar'
            
          },{
            name: 'B相',
            data: [],
            type: 'bar'
          },{
            name: 'C相',
            data: [],
            type: 'bar'
          },{
            name: '总体',
            data: [],
            type: 'bar'
          },{
            name: '需量负荷',
            data: [],
            type: 'bar'
          },{
            name: '零线电流',
            data: [],
            type: 'bar'
          },{
            name: 'Uab',
            data: [],
            type: 'bar'
          },{
            name: 'Ubc',
            data: [],
            type: 'bar'
          },{
            name: 'Uca',
            data: [],
            type: 'bar'
          },{
            name: '电流',
            data: [],
            type: 'bar'
          },{
            name: '电压',
            data: [],
            type: 'bar'
          }

        ],

      },


      changeBG: [0,0,0,0,0,0,0,0,0],
      tableData: []
    }
  },
  


  created(){
    this.convert()
  },
  mounted() {
    
    let myChartYear = echarts.init(document.getElementById("myChartYear"), "light"); // 初始化echarts, theme为light
    myChartYear.setOption(this.echartsOption);
    this.fhclick()
// echarts设置选项s
    
  },


  methods:{


    handleCommand(command) {
      if(command=="a"){        
        this.getPdf()
      }
    },
    //时间转化函数
    changeTime(A){
      var date = new Date(A*1000 + 8 * 3600 * 1000);
      var B = date.toJSON().substr(0, 19).replace('T', ' ').replace(/-/g, '.')
      return B
    },


    //------------------------------------------图数据处理----------------------------------------
    addLineYear(){
      this.echartsOption.legend.data=this.checkListYear
      //判断某些元素是否存在，如果存在就做出时间
      if(this.checkListYear.indexOf('A相')!=-1){
        this.echartsOption.legend.selected['A相'] = true
        
      }else{
        this.echartsOption.legend.selected['A相'] = false
      
      }

      if(this.checkListYear.indexOf('B相')!=-1){
        this.echartsOption.legend.selected['B相'] = true
      }else{
        this.echartsOption.legend.selected['B相'] = false
        
      }

      if(this.checkListYear.indexOf('C相')!=-1){

        this.echartsOption.legend.selected['C相'] = true
       
      }else{

        this.echartsOption.legend.selected['C相'] = false
       
      }
      if(this.checkListYear.indexOf('总体')!=-1){
        this.echartsOption.legend.selected['总体'] = true 
      }else{
  
        this.echartsOption.legend.selected['总体'] = false
      }
      if(this.checkListYear.indexOf('需量负荷')!=-1){
        this.echartsOption.legend.selected['需量负荷'] = true
      }else{
        this.echartsOption.legend.selected['需量负荷'] = false  
      }
      if(this.checkListYear.indexOf('零线电流')!=-1){
        this.echartsOption.legend.selected['零线电流'] = true
      }else{
        this.echartsOption.legend.selected['零线电流'] = false
      }
      if(this.checkListYear.indexOf('Uab')!=-1){
        this.echartsOption.legend.selected['Uab'] = true
      }else{
        this.echartsOption.legend.selected['Uab'] = false 
      }
      if(this.checkListYear.indexOf('Ubc')!=-1){
        this.echartsOption.legend.selected['Ubc'] = true
      }else{
        this.echartsOption.legend.selected['Ubc'] = false
      }
      if(this.checkListYear.indexOf('Uca')!=-1){
        this.echartsOption.legend.selected['Uca'] = true
      }else{
        this.echartsOption.legend.selected['Uca'] = false
      }
      if(this.checkListYear.indexOf('电流')!=-1){
        this.echartsOption.legend.selected['电流'] = true
      }else{
        this.echartsOption.legend.selected['电流'] = false
      }
      if(this.checkListYear.indexOf('电压')!=-1){
        this.echartsOption.legend.selected['电压'] = true
      }else{
        this.echartsOption.legend.selected['电压'] = false
      }

      let myChartYear = echarts.init(document.getElementById("myChartYear"), "light"); // 初始化echarts, theme为light
      myChartYear.setOption(this.echartsOption);
    },
    cleanLine(){
      // let i=0
      // for(i=0;i<9;i++){
      //   this.echartsOption.legend.data[i] = ''
      // }
      this.checkListYear=[]
      this.echartsOption.yAxis.min=0
      this.echartsOption.legend.data=[]
      this.echartsOption.legend.selected['A相']=false
      this.echartsOption.legend.selected['B相']=false
      this.echartsOption.legend.selected['C相']=false
      this.echartsOption.legend.selected['总体']=false
      this.echartsOption.legend.selected['需量负荷']=false
      this.echartsOption.legend.selected['零线电流']=false
      this.echartsOption.legend.selected['Uab']=false
      this.echartsOption.legend.selected['Ubc']=false
      this.echartsOption.legend.selected['Uca']=false
      this.echartsOption.legend.selected['频率偏差']=false
      this.echartsOption.legend.selected['电流']=false
      this.echartsOption.legend.selected['电压']=false


      
    },
    changeLine(){
      //this.convert()
      
      if(this.lineValue == '平均负荷'){//负荷变量赋值
        //选择框赋值
        this.cleanLine()
        this.isshowYear=''
        this.isshowYear0='',
        this.isshowYear1='display:none',
        this.isshowYear2='display:none',
        this.isshowYear3='display:none',
        this.isshowYear4='display:none'

        this.checkListMonth=['总体']
        this.echartsOption.legend.data=['总体']
        this.echartsOption.legend.selected['总体']=true
        this.echartsOption.yAxis.name = this.lineValue+'(kW)'
        let i=0
        while(i<this.yearChartdata.length){
          //时间戳变量转化成时间变量
          
           
          this.echartsOption.xAxis.data[i] = i
          this.echartsOption.series[0].data[i]=this.yearChartdata[i].aLoad//给A相负荷赋值
          this.echartsOption.series[1].data[i]=this.yearChartdata[i].bLoad//给B相负荷赋值
          this.echartsOption.series[2].data[i]=this.yearChartdata[i].cLoad//给C相负荷赋值
          this.echartsOption.series[3].data[i]=this.yearChartdata[i].totalLoad//给总体负荷赋值
          
          i=i+1
        }
        

      }
      else if(this.lineValue == '最大负荷'){//负荷变量赋值
        //选择框赋值
        this.cleanLine()
        this.isshowYear=''
        this.isshowYear0='',
        this.isshowYear1='display:none',
        this.isshowYear2='display:none',
        this.isshowYear3='display:none',
        this.isshowYear4='display:none'

        this.echartsOption.yAxis.name = this.lineValue+'(kVar)'
        this.checkListYear=['总体']
        this.echartsOption.legend.data=['总体']
        this.echartsOption.legend.selected['总体']=true
        let i=0
        while(i<this.yearChartdataMax.length){
          //时间戳变量转化成时间变量
          
           
          this.echartsOption.xAxis.data[i] = i
          this.echartsOption.series[0].data[i]=this.yearChartdataMax[i].aLoad//给A相负荷赋值
          this.echartsOption.series[1].data[i]=this.yearChartdataMax[i].bLoad//给B相负荷赋值
          this.echartsOption.series[2].data[i]=this.yearChartdataMax[i].cLoad//给C相负荷赋值
          this.echartsOption.series[3].data[i]=this.yearChartdataMax[i].totalLoad//给总体负荷赋值
          
          i=i+1
        }
        

      }
      else if(this.lineValue == '无功功率'){//给无功功率赋值
        //选择框赋值
        this.cleanLine()
        this.isshowYear=''
        this.isshowYear0='',
        this.isshowYear1='display:none',
        this.isshowYear2='display:none',
        this.isshowYear3='display:none'
        this.isshowYear4='display:none'
        this.echartsOption.yAxis.name = this.lineValue+'(kVar)'
       
      }
      else if(this.lineValue == '功率因数'){//给功率因数赋值
        //选择框赋值
        this.cleanLine()
        this.isshowYear=''
        this.isshowYear0='',
        this.isshowYear1='display:none',
        this.isshowYear2='display:none',
        this.isshowYear3='display:none',
        this.isshowYear4='display:none',


        this.echartsOption.yAxis.name = this.lineValue
      }
      else if(this.lineValue == '电流'){//给电流赋值
        //选择框赋值
        this.cleanLine()
        this.isshowYear=''
        this.isshowYear0='display:none',
        this.isshowYear1='display:none',
        this.isshowYear2='',
        this.isshowYear3='display:none',
        this.isshowYear4='display:none',

        this.echartsOption.yAxis.name = this.lineValue+'(A)'
      }
      else if(this.lineValue == '电压'){//给电压赋值
        //选择框赋值
        this.cleanLine()
        this.isshowYear=''
        this.isshowYear0='display:none',
        this.isshowYear1='display:none',
        this.isshowYear2='display:none',
        this.isshowYear3='',
        this.isshowYear4='display:none',

        this.echartsOption.yAxis.name = this.lineValue+'(V)'
      }
      let myChartYear = echarts.init(document.getElementById("myChartYear"), "light"); // 初始化echarts, theme为light
      myChartYear.setOption(this.echartsOption);

    },

    //----------------------------------------------表格数据处理-----------------------------------
    cleanTable(){
      this.tableData=[{
          message: '',
          average: '',
          maxValue: '',
          maxTime: '',
          minValue: '',
          minTime: ''
        },{
          message: '',
          average: '',
          maxValue: '',
          maxTime: '',
          minValue: '',
          minTime: ''
        },{
          message: '',
          average: '',
          maxValue: '',
          maxTime: '',
          minValue: '',
          minTime: ''
        },{
          message: '',
          average: '',
          maxValue: '',
          maxTime: '',
          minValue: '',
          minTime: ''
        }
      ]
    },


    fhclick(){
      this.changeBG = [1,0,0,0,0,0,0,0,0]
      this.cleanTable()

      this.tableData[0].message = '总负荷(kW)'
      this.tableData[1].message = 'A相负荷(kW)'
      this.tableData[2].message = 'B相负荷(kW)'
      this.tableData[3].message = 'C相负荷(kW)'

      this.tableData[0].average = this.tableAvgDataYear.totalLoad
      this.tableData[1].average = this.tableAvgDataYear.aLoad
      this.tableData[2].average = this.tableAvgDataYear.bLoad
      this.tableData[3].average = this.tableAvgDataYear.cLoad

      this.tableData[0].maxValue = this.tableMaxDataYear.totalLoad
      this.tableData[1].maxValue = this.tableMaxDataYear.aLoad
      this.tableData[2].maxValue = this.tableMaxDataYear.bLoad
      this.tableData[3].maxValue = this.tableMaxDataYear.cLoad

      this.tableData[0].minValue = this.tableMinDataYear.totalLoad
      this.tableData[1].minValue = this.tableMinDataYear.aLoad
      this.tableData[2].minValue = this.tableMinDataYear.bLoad
      this.tableData[3].minValue = this.tableMinDataYear.cLoad

      this.tableData[0].maxTime = this.changeTime(this.tableMaxDataYear.totalLoadTime)
      this.tableData[1].maxTime = this.changeTime(this.tableMaxDataYear.aLoadTime)
      this.tableData[2].maxTime = this.changeTime(this.tableMaxDataYear.bLoadTime)
      this.tableData[3].maxTime = this.changeTime(this.tableMaxDataYear.cLoadTime)

      this.tableData[0].minTime = this.changeTime(this.tableMinDataYear.totalLoadTime)
      this.tableData[1].minTime = this.changeTime(this.tableMinDataYear.aLoadTime)
      this.tableData[2].minTime = this.changeTime(this.tableMinDataYear.bLoadTime)
      this.tableData[3].minTime = this.changeTime(this.tableMinDataYear.cLoadTime)
    },
    wgglclick(){
      this.changeBG = [0,1,0,0,0,0,0,0,0]
      this.cleanTable()
      this.tableData[0].message = '总无功(kVar)'
      this.tableData[1].message = 'A相无功(kVar)'
      this.tableData[2].message = 'B相无功(kVar)'
      this.tableData[3].message = 'C相无功(kVar)'

      this.tableData[0].average = this.tableAvgDataYear.totalReactivePower
      this.tableData[1].average = this.tableAvgDataYear.aReactivePower
      this.tableData[2].average = this.tableAvgDataYear.bReactivePower
      this.tableData[3].average = this.tableAvgDataYear.cReactivePower

      this.tableData[0].maxValue = this.tableMaxDataYear.totalReactivePower
      this.tableData[1].maxValue = this.tableMaxDataYear.aReactivePower
      this.tableData[2].maxValue = this.tableMaxDataYear.bReactivePower
      this.tableData[3].maxValue = this.tableMaxDataYear.cReactivePower

      this.tableData[0].minValue = this.tableMinDataYear.totalReactivePower
      this.tableData[1].minValue = this.tableMinDataYear.aReactivePower
      this.tableData[2].minValue = this.tableMinDataYear.bReactivePower
      this.tableData[3].minValue = this.tableMinDataYear.cReactivePower

      this.tableData[0].maxTime = this.changeTime(this.tableMaxDataYear.totalReactivePowerTime)
      this.tableData[1].maxTime = this.changeTime(this.tableMaxDataYear.aReactivePowerTime)
      this.tableData[2].maxTime = this.changeTime(this.tableMaxDataYear.bReactivePowerTime)
      this.tableData[3].maxTime = this.changeTime(this.tableMaxDataYear.cReactivePowerTime)

      this.tableData[0].minTime = this.changeTime(this.tableMinDataYear.totalReactivePowerTime)
      this.tableData[1].minTime = this.changeTime(this.tableMinDataYear.aReactivePowerTime)
      this.tableData[2].minTime = this.changeTime(this.tableMinDataYear.bReactivePowerTime)
      this.tableData[3].minTime = this.changeTime(this.tableMinDataYear.cReactivePowerTime)
    },
    glysclick(){
      this.changeBG = [0,0,1,0,0,0,0,0,0]
      this.cleanTable()
      this.tableData[0].message = '总功率因数'
      this.tableData[1].message = 'A相功率因数'
      this.tableData[2].message = 'B相功率因数'
      this.tableData[3].message = 'C相功率因数'

      this.tableData[0].average = this.tableAvgDataYear.totalPowerFactor
      this.tableData[1].average = this.tableAvgDataYear.aPowerFactor
      this.tableData[2].average = this.tableAvgDataYear.bPowerFactor
      this.tableData[3].average = this.tableAvgDataYear.cPowerFactor

      this.tableData[0].maxValue = this.tableMaxDataYear.totalPowerFactor
      this.tableData[1].maxValue = this.tableMaxDataYear.aPowerFactor
      this.tableData[2].maxValue = this.tableMaxDataYear.bPowerFactor
      this.tableData[3].maxValue = this.tableMaxDataYear.cPowerFactor

      this.tableData[0].minValue = this.tableMinDataYear.totalPowerFactor
      this.tableData[1].minValue = this.tableMinDataYear.aPowerFactor
      this.tableData[2].minValue = this.tableMinDataYear.bPowerFactor
      this.tableData[3].minValue = this.tableMinDataYear.cPowerFactor

      this.tableData[0].maxTime = this.changeTime(this.tableMaxDataYear.totalPowerFactorTime)
      this.tableData[1].maxTime = this.changeTime(this.tableMaxDataYear.aPowerFactorTime)
      this.tableData[2].maxTime = this.changeTime(this.tableMaxDataYear.bPowerFactorTime)
      this.tableData[3].maxTime = this.changeTime(this.tableMaxDataYear.cPowerFactorTime)

      this.tableData[0].minTime = this.changeTime(this.tableMinDataYear.totalPowerFactorTime)
      this.tableData[1].minTime = this.changeTime(this.tableMinDataYear.aPowerFactorTime)
      this.tableData[2].minTime = this.changeTime(this.tableMinDataYear.bPowerFactorTime)
      this.tableData[3].minTime = this.changeTime(this.tableMinDataYear.cPowerFactorTime)
    },
    dlclick(){
      this.changeBG = [0,0,0,1,0,0,0,0,0]
      this.cleanTable()
      this.tableData[0].message = 'A相电流(A)'
      this.tableData[1].message = 'B相电流(A)'
      this.tableData[2].message = 'C相电流(A)'
      this.tableData[3].message = '零线电流(A)'

      this.tableData[0].average = this.tableAvgDataYear.ia
      this.tableData[1].average = this.tableAvgDataYear.ib
      this.tableData[2].average = this.tableAvgDataYear.ic
      this.tableData[3].average = this.tableAvgDataYear.zeroi

